.hero {
	height: 100vh;

	&__title {
		grid-row: 5;
		grid-column: 2 / 6;
		align-self: center;
		justify-self: center;

		@include respond-to('medium') {
			grid-row: 2 / 5;
			grid-column: 2 / 4;
			justify-self: start;
		}
	}

	&__content {
		grid-row: 6;
		grid-column: 2 / 6;
		align-self: start;
		justify-self: start;

		@include respond-to('medium') {
			grid-row: 5;
			grid-column: 2 / 4;
			align-self: start;
			justify-self: start;
			white-space: normal;
		}
	}
}

.profile {
	position: relative;
	grid-column: 2 / 6;
	grid-row: 1 / 5;
	overflow: hidden;

	@include respond-to('medium') {
		grid-column: 4 / -1;
		grid-row: 1 / -1;
	}

	&__picture {
		position: absolute;
		top: -12.5%;
		left: -12.5%;
		height: 125%;
		width: 125%;
		-o-object-fit: cover;
		object-fit: cover;
		transition: transform .1s;
		transform: translate(calc((var(--mouse-x)/var(--viewport-x)-.5) * 10px), calc((var(--mouse-y)/var(--viewport-y)-.5) * 10px));
	}
}

.type-writer {
	font-family: 'ibm plex mono', sans-serif;
	font-size: calc(1rem + 1vw);
	white-space: nowrap;

	@include respond-to('medium') {
		align-self: start;
		justify-self: start;
		white-space: normal;
	}
}

.ti-cursor {
	margin-left: .1em !important;
	font-size: calc(1.5rem + 1vw) !important;
}

.tab {
	position: relative;
	grid-row: 2 / -1;
	grid-column: 1;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;

	&__item {
		-webkit-writing-mode: vertical-lr;
		-ms-writing-mode: tb-lr;
		writing-mode: vertical-lr;
		font-family: 'ibm plex mono', sans-serif;
		font-size: calc(1rem + 1vw);
		line-height: 1.6;
		transform: rotate(180deg);
		transform-origin: center center;

		&__link {
			color: #000;
		}

	}
}

@-webkit-keyframes scroll {
	0% {
		opacity: 0;
	}

	50% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes scroll {
	0% {
		opacity: 0;
	}

	50% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

@keyframes scroll {
	0% {
		transform: translateY(0);
		opacity: 0;
	}

	10% {
		transform: translateY(0);
		opacity: 1;
	}

	30% {
		transform: translateY(0);
		height: 4px;
	}

	50% {
		transform: translateY(0);
		height: 60px;
	}

	80% {
		transform: translateY(60px);
		height: 4px;
		opacity: 1;
	}

	90% {
		opacity: 0;
	}

	100% {
		transform: translateY(15px);
		opacity: 0;
	}
}

.scroll {
	position: absolute;
	right: 0;
	bottom: 0;
	margin: 5%;
	display: flex;
	flex-direction: column;

	&__line {
		position: relative;
		width: 4px;
		height: 4px;
		background: #000;
		-webkit-animation: scroll 3s cubic-bezier(.77, 0, .175, 1) infinite;
		animation: scroll 3s cubic-bezier(.77, 0, .175, 1) infinite;
	}
}